<template>
  <section class="wrap main-wrap single clearfix" ref="content">
    <div class="loading" v-if="JSON.stringify(articleContent) == '{}'">
      <img src="../../../static/images/loading.svg" alt="">
    </div>
    <div class="fl content-wrap" v-else>
      <header class="content-header">
        <div class="breadcrumbs">当前位置： 首页 > 关于我 > 测试</div>
        <h2 class="title text-center">{{ articleContent.title.rendered }}</h2>
        <div class="article-info text-center">
          <a href="#">{{ articleContent.articleInfor.auther }}</a>
          <span class="text">发表于:</span>
          <time>{{ articleContent.date.replace('T', ' ') }}</time>
          <span class="text">分类:</span>
          <a href="#" v-for="(item, index) in articleContent.articleInfor.classify" :key="item.key">{{ item.name }}{{ index == articleContent.articleInfor.classify.length - 1 ? '' : '、'  }}</a>
          <i class="iconfont icon-message"></i>
          <span class="text">{{ articleContent.articleInfor.commentCount }}</span>
          <i class="iconfont icon-hot"></i>
          <span class="text">{{ articleContent.articleInfor.viewCount }}</span>
        </div>
      </header>
      <div class="content" v-html="articleContent.content.rendered"></div>
      <!-- 发表意见 -->
      <ul class="opinion">
        <li class="list">
          <a href="#">
            <span class="block">29人</span>
            <img src="../../assets/images/like_love.png" alt="">
            <span class="block">Love</span>
          </a>
        </li>
        <li class="list">
          <a href="#">
            <span class="block">29人</span>
            <img src="../../assets/images/like_haha.png" alt="">
            <span class="block">Haha</span>
          </a>
        </li>
        <li class="list">
          <a href="#">
            <span class="block">29人</span>
            <img src="../../assets/images/like_wow.png" alt="">
            <span class="block">Wow</span>
          </a>
        </li>
        <li class="list">
          <a href="#">
            <span class="block">29人</span>
            <img src="../../assets/images/like_sad.png" alt="">
            <span class="block">Sad</span>
          </a>
        </li>
        <li class="list">
          <a href="#">
            <span class="block">29人</span>
            <img src="../../assets/images/link_angry.png" alt="">
            <span class="block">Angry</span>
          </a>
        </li>
      </ul>
      <!-- 分享 -->
      <div class="share text-center">
        <span class="text">分享到：</span>
        <a href="#">
          <svg class="iconfont-colour" aria-hidden="true">
            <use xlink:href="#icon-QQ"></use>
          </svg>
        </a>
        <a href="#">
          <svg class="iconfont-colour" aria-hidden="true">
            <use xlink:href="#icon-Qzone"></use>
          </svg>
        </a>
        <a href="#">
          <svg class="iconfont-colour" aria-hidden="true">
            <use xlink:href="#icon-weichat"></use>
          </svg>
        </a>
        <a href="#">
          <svg class="iconfont-colour" aria-hidden="true">
            <use xlink:href="#icon-xinlang"></use>
          </svg>
        </a>
      </div>
      <!-- 标签 -->
      <div class="tag-list text-center">
        <i class="iconfont icon-tag"></i>
        <a href="#">测试文章</a>
        <a href="#">测试文章</a>
      </div>
      <!-- 作者专栏 -->
      <div class="auther-introduct">

      </div>
    </div>
    <sidebar></sidebar>
  </section>
</template>
<script>
import sidebar from '@/components/common/Sidebar'
import store from '@/vuex/store'
import axios from 'axios'
// import Prism from '../../../static/js/prism'
export default {
  name: 'single',
  components: {
    sidebar
  },
  store,
  data: () => ({
    articleContent: {}
  }),
  mounted () {
    axios.get(`/wordpress-4.7.4/wp-json/wp/v2/posts/${this.$route.params.id}`)
      .then((res) => {
        this.articleContent = res.data
        // console.log(this.articleContent)
        // console.log(this.$refs.content.querySelector('.content-wrap'))
        // Prism.highlightAll(this.$refs.content.querySelector('.content-wrap'))
      }).catch((err) => console.log(err))
  }
}
</script>
<style lang="scss" scoped>
@import "../../assets/scss/_common.scss";
.main-wrap{
  .loading{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.8);

    img{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .content-wrap{
    box-sizing: border-box;
    width: 850px;
    padding: 15px;
    border-radius: 5px;
    background: #fff;
  }

  // 文章基本信息
  .article-info{
    margin: 10px 0;
    padding-bottom: 5px;
    border-bottom: border-1($colorGay1);
    color: $colorGay3;
  }

  // 发表意见
  .opinion{
    width: 50%;
    margin: 20px auto;
    @extend %navJustify;

    .list{
      display: inline-block;
      text-align: center;

      span{
        &:first-of-type{
          font-size: 12px;
        }
      }

      img{
        width: 50px;
      }
    }
  }

  // 标签
  .tag-list{
    margin: 20px 0;

    .icon-tag{
      font-size: 18px;
    }
  }
}
</style>



